.custom-navbar { 
	background-color:cornflowerblue; 
	padding: 1rem 0;
	text-align:center; 
} 

.FontAwesomeIcon { 
	color: white; 
} 

span { 
	color: white; 
} 

.brand-name { 
	font-family: 'Pacifico', cursive; 
	font-size: 2rem; 
	font-weight: bold; 
	color: #fff; 
	margin-left: 20px; 
	text-decoration: none; 
	transition: color 0.3s; 
} 

.brand-name:hover { 
	color: #17a2b8; 
} 

.navbar-toggler-icon { 
	background-color: #fff; 
} 

.nav-link { 
	color: #fff; 
	margin-right: 15px; 
	display: center; 
	align-items: center; 
	transition: color 0.3s; 
} 

.nav-link span { 
	margin-right: 0.5rem; 
} 

.nav-link:hover, 
.ho:hover, 
.nav-link:hover span { 
	color: black !important; 
} 

.navbar-toggler { 
	border: none; 
} 

.navbar-toggler:focus { 
	outline: none; 
} 

.ml-auto { 
	margin-left: auto; 
} 

/* Font Awesome Icons */
.fa-lg { 
	margin-right: 5px; 
} 

/* Responsive Styles */
@media (max-width: 768px) { 
	.navbar-nav { 
		margin-left: 0; 
		text-align: center; 
	} 

	.navbar-toggler { 
		margin-bottom: 10px; 
	} 

	.ml-auto { 
		margin-left: 0; 
		text-align: center; 
		margin-top: 10px; 
	} 
}
